// 引入未审核组件
import meterTable from '@/views/main/EnergyAllocation/meterTable.vue';
import { PAGE_TITLE, STATUS_CODE, TITLE } from '@/utils/constantsFile';
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 搜索组建
import SearchBox from '@/components/common/SearchBox';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import { getListQueryParameter } from '@/api/energyalllocation';
// // 引入审核通过组件
// import waterMeter from '@/views/main/EnergyAllocation/waterMeter.vue';
// // 引入审核未通过组件
// import gasMeter from '@/views/main/EnergyAllocation/gasMeter.vue';
export default {
  name: 'EnergyAllocation',
  data() {
    return {
      PAGE_TITLE: PAGE_TITLE,
      type: 'ELECTRICITY',
      // tab默认激活下标
      active: 0,
      // 搜索框有关
      isSearchBoxShow: false,
      searchList: [],
      searchData: [],
      // 默认引入的组件名称
      currentView: 'electricityMeter',

      tabs: [
        {
          type: 'electricityMeter',
          name: PAGE_TITLE.ELECTRICITY_METER,
          value: 'ELECTRICITY'
        },
        {
          type: 'waterMeter',
          name: PAGE_TITLE.WATER_METER,
          value: 'WATER'
        },
        {
          type: 'gasMeter',
          name: PAGE_TITLE.GAS_NAME,
          value: 'GAS'
        }
      ]
    };
  },
  created() {
    this.getSearchList();
  },
  methods: {
    /**
     * @description: 切换tab事件
     * @param i 下标
     * @param v 组件名
     * @return: 无
     * @author: ZJM
     */
    toggle(i, v) {
      this.active = i;
      this.type = v;
    },
    /**
     * @description: 获取查询条件
     * @param 无
     * @return: 无
     * @author: CH
     */
    async getSearchList() {
      try {
        const res = await getListQueryParameter({});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // showSuccessMessage(res.message);
          // this.currentChangeRow = {};
          this.searchList = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: （主页面）点击“搜索”按钮，打开搜索框
     * @param 无
     * @return: void
     * @author: cyb
     */
    showSearchCard() {
      // alert(1321);
      this.isSearchBoxShow = !this.isSearchBoxShow;
    },
    /**
     * @description:处理查询数据
     * @param {type}
     * @return:
     * @author: CH
     */
    findTable(conditionArr1, groupOperator, conditionArr2) {
      this.$refs.meterTable.findTable(
        conditionArr1,
        groupOperator,
        conditionArr2
      );
    }
  },
  components: {
    // 加载未审核组件
    meterTable,
    HeaderTitle,
    SearchBox
  }
};
