// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 导入所需接口
import {
  EnergyGetMeter,
  EnergyInsertOrUpdate,
  EnergyGetMeterDropdown
} from '@/api/energyalllocation';
import {
  PAGE_TITLE,
  BTN_TEXT,
  STATUS_CODE,
  TITLE,
  CONSTANTS_CODE,
  MSG
} from '@/utils/constantsFile';
export default {
  components: {
    // 加载通用table组件
    VXETable
  },
  props: ['type'],
  data() {
    return {
      // 可编辑配
      PAGE_TITLE: PAGE_TITLE,
      CONSTANTS_CODE: CONSTANTS_CODE,
      BTN_TEXT: BTN_TEXT,
      restaurants: [],
      orderByData: {},
      deviceName: '',
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: false
        // icon: 'fa fa-file-text-o'
      },
      searchData: [],
      // table是否加载
      tableLoading: false,
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: 'deviceName',
          title: PAGE_TITLE.METER_NAME,
          width: '',
          align: 'center',
          sortable: true
        },
        {
          field: 'averageFlow',
          title: PAGE_TITLE.AVERAGE_FLOW,
          width: '',
          align: 'center'
          // sortable: true
        },
        {
          field: 'hoursFlow',
          title: '小时流量限制',
          width: '',
          align: 'center',
          type: 'html',
          sortable: true,
          slots: {
            default: ({ row, column, cellValue }) => {
              return [
                <div>
                  <div v-show={row.showFlag}>
                    <span style="width:50px;display: inline-block;">
                      {row.hoursFlow}
                    </span>

                    <el-button
                      type="text"
                      onClick={() => this.showChange(row, column)}
                    >
                      {BTN_TEXT.EDIT}
                    </el-button>
                  </div>
                  <div v-show={!row.showFlag}>
                    <el-input
                      type="number"
                      size="mini"
                      v-model={row.showVal}
                      oninput={() => this.clean(row)}
                    ></el-input>
                    <el-button
                      type="text"
                      onClick={() => this.success(row, column)}
                    >
                      {BTN_TEXT.CONFIRM}
                    </el-button>
                    <el-button
                      type="text"
                      style="color:red"
                      onClick={() => this.showChange(row, column)}
                    >
                      {BTN_TEXT.CANCEL}
                    </el-button>
                  </div>
                </div>
              ];
            }
          }
        }
      ],
      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      }
    };
  },
  watch: {
    // 发现type发生变化触发事件
    type(val) {
      this.restaurants = [];
      this.deviceName = '';
      this.orderByData = {};
      // this.findDevice();
      this.onTablePageChange(1, 10);
    }
  },
  created() {
    // 调用列表查询接口
    this.findList();
    // this.findDevice();
  },
  methods: {
    clean(row) {
      if (row.showVal.length > 5) {
        row.showVal = row.showVal.slice(0, 5);
      }
    },
    /**
     * @description:表格排序
     * @return:
     * @author: CH
     */
    sortChange(property, order) {
      this.orderByData = {};
      this.orderByData[property] = order;
      this.onTablePageChange(1, this.tablePage.pageSize);
    },
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList() {
      try {
        this.tableLoading = true;
        const res = await EnergyGetMeter(
          {
            size: this.tablePage.pageSize,
            current: this.tablePage.currentPage,
            type: this.type
          },
          {
            conditionJson1: this.searchData.conditionArr1,
            operator: this.searchData.groupOperator,
            conditionJson2: this.searchData.conditionArr2,
            orderBy: this.orderByData
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          const arr = res.rows;
          this.tableDatas = arr.map(val => {
            let json = {};

            json = val;

            json.showFlag = 1;
            json.showVal = val.hoursFlow;
            return json;
          });
          // 将总条数写入table分页配置
          this.tablePage.total = res.total;
          // 加载动画消失
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 点击确认修改
     * @param row 当前行数据
     * @param column 表头内容
     * @return: 无
     * @author: CH
     */
    success(row, column) {
      if (row.showVal < 0) {
        showWarningMessage('请输入大于等于零的数字！');
        return false;
      }
      // 确认是否执行操作
      this.$confirm(MSG.ARE_YOU_SURE_TO_MODIFY, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          // 调用执行审核通过的方法
          this.setSuccess(row);
        })
        .catch(() => { });
    },
    /**
     * @description: 执行修改的方法
     * @param row  当前行数据
     * @return: 无
     * @author: CH
     */
    async setSuccess(row) {
      try {
        // this.tableLoading = true;
        const res = await EnergyInsertOrUpdate({
          deviceId: row.deviceId,
          hoursFlow: row.showVal
        });
        if (res.code) {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // 调用列表查询接口
            this.findList();
            // 提示异常信息
            showSuccessMessage(res.message);
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:进入编辑状态
     * @param
     * @return:
     * @author: CH
     */
    showChange(row) {
      row.showFlag = !row.showFlag;
      row.showVal = row.hoursFlow;
    },
    /**
     * @description:查询设备
     * @param{row}
     * @return:{json}
     * @author: CH
     */
    async findDevice() {
      try {
        this.tableLoading = true;
        const res = await EnergyGetMeterDropdown(
          {
            type: this.type
          },
          [
            {
              column: 'state', // 参数名
              operator: 1, // 并且
              ptype: 'string', // 参数类型
              type: 'in', // 包含
              value: 'electricity' // 参数值
            }
          ]
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入选项
          this.restaurants = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:输入查询
     * @param
     * @return:
     * @author: CH
     */
    querySearch(queryString, cb) {
      var restaurants = this.restaurants;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return restaurant.deviceName.toLowerCase().indexOf(queryString) !== -1;
      };
    },
    handleSelect(ob) {
      this.deviceName = ob.deviceName;
    },
    /**
     * @description:重置
     * @param
     * @return:
     * @author: CH
     */
    reset() {
      this.deviceName = '';
      this.onTablePageChange(1, 10);
    },
    /**
     * @description:处理查询数据
     * @param {type}
     * @return:
     * @author: CH
     */
    findTable(conditionArr1, groupOperator, conditionArr2) {
      this.searchData = [];
      if (conditionArr1 !== undefined) {
        this.searchData.conditionArr1 = JSON.parse(
          JSON.stringify(conditionArr1)
        );
        for (var i in this.searchData.conditionArr1) {
          this.searchData.conditionArr1[
            i
          ].value = this.searchData.conditionArr1[i].value.toString();
        }
      }
      if (groupOperator !== undefined) {
        this.searchData.groupOperator = groupOperator;
      }
      if (conditionArr2 !== undefined) {
        this.searchData.conditionArr2 = JSON.parse(
          JSON.stringify(conditionArr2)
        );
        for (var x in this.searchData.conditionArr2) {
          this.searchData.conditionArr2[
            x
          ].value = this.searchData.conditionArr2[x].value.toString();
        }
      }
      this.onTablePageChange(1, this.tablePage.pageSize);
    }
  }
};
